"use client";

import { useEffect, useMemo, useState } from "react";
import { useReactTable, getCoreRowModel, getPaginationRowModel, getSortedRowModel } from "@tanstack/react-table";
import { Pencil, Trash2, Search, X } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardHeader, CardTitle, CardToolbar, CardTable, CardFooter } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { ScrollArea, ScrollBar } from "@/components/ui/scroll-area";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { DataGrid } from "@/components/ui/data-grid";
import { DataGridTable } from "@/components/ui/data-grid-table";
import { DataGridPagination } from "@/components/ui/data-grid-pagination";
import Image from "next/image";
import { useProCategory } from "./pro-category";

export default function ProCategoryTable() {
    const { proCategories, setProCategories, setProCategoryToEdit, refreshFlag } = useProCategory();
    const [loading, setLoading] = useState(true);
    const [searchQuery, setSearchQuery] = useState("");
    const [pagination, setPagination] = useState({ pageIndex: 0, pageSize: 10 });

    useEffect(() => {
        const fetchProCategories = async () => {
            try {
                setLoading(true);
                const token = localStorage.getItem("token");
                const res = await fetch("https://wiqiapi.testenvapp.com/api/admin/getProSubCategory", {
                    headers: { Authorization: `Bearer ${token}` },
                });
                const data = await res.json();
                if (data.success) {
                    setProCategories(data.data.data || []);
                }
            } catch (err) {
                console.error(err);
            } finally {
                setLoading(false);
            }
        };
        fetchProCategories();
    }, [refreshFlag]);

    const filteredData = useMemo(() => {
        if (!searchQuery) return proCategories;
        return proCategories.filter(item =>
            item.title?.toLowerCase().includes(searchQuery.toLowerCase()) ||
            item.type?.toLowerCase().includes(searchQuery.toLowerCase()) ||
            item.categoryId?.name?.toLowerCase().includes(searchQuery.toLowerCase())
        );
    }, [searchQuery, proCategories]);

    const columns = useMemo(() => [
        {
            id: "image",
            accessorFn: row => row.image,
            header: "Image",
            cell: ({ row }) =>
                row.original.image ? (
                    <Image src={row.original.image} alt="img" width={48} height={48} className="rounded border" />
                ) : "Null",
            size: 80,
        },
        {
            id: "category",
            accessorFn: row => row.categoryId?.name,
            header: "Category",
            cell: ({ row }) => row.original.categoryId?.name || "Null",
        },
        {
            id: "subCategory",
            accessorFn: row => row.subCategoryId?.name,
            header: "SubCategory",
            cell: ({ row }) => row.original.subCategoryId?.name || "Null",
        },
        {
            id: "title",
            accessorFn: row => row.title,
            header: "Title",
            cell: ({ row }) => row.original.title,
        },
        {
            id: "type",
            accessorFn: row => row.type,
            header: "Type",
            cell: ({ row }) => row.original.type,
        },
        {
            id: "createdAt",
            accessorFn: row => row.createdAt,
            header: "Created At",
            cell: ({ row }) => new Date(row.original.createdAt).toLocaleDateString(),
        },
        {
            id: "actions",
            header: "Actions",
            cell: ({ row }) => (
                <div className="flex gap-2 justify-end">
                    <button onClick={() => setProCategoryToEdit(row.original)} className="text-primary">
                        <Pencil size={16} />
                    </button>
                    <button className="text-red-600">
                        <Trash2 size={16} />
                    </button>
                </div>
            ),
        }
    ], [setProCategoryToEdit]);

    const table = useReactTable({
        columns,
        data: filteredData,
        pageCount: Math.ceil(filteredData.length / pagination.pageSize),
        state: { pagination },
        onPaginationChange: setPagination,
        getCoreRowModel: getCoreRowModel(),
        getPaginationRowModel: getPaginationRowModel(),
        getSortedRowModel: getSortedRowModel(),
    });

    if (loading) return <div className="p-6">Loading...</div>;

    return (
        <DataGrid table={table} recordCount={filteredData.length}>
            <Card>
                <CardHeader className="py-3.5">
                    <CardTitle>Pro Categories</CardTitle>
                    <CardToolbar className="relative">
                        <Search className="absolute start-2 top-1/2 -translate-y-1/2 text-primary" />
                        <Input
                            placeholder="Search..."
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                            className="ps-9 max-w-[300px]"
                        />
                        {searchQuery && (
                            <Button variant="ghost" className="absolute end-1 top-1/2 -translate-y-1/2 h-6 w-6" onClick={() => setSearchQuery("")}>
                                <X />
                            </Button>
                        )}
                    </CardToolbar>
                </CardHeader>
                <CardTable>
                    <ScrollArea>
                        <DataGridTable />
                        <ScrollBar orientation="horizontal" />
                    </ScrollArea>
                </CardTable>
                <CardFooter>
                    <DataGridPagination />
                </CardFooter>
            </Card>
        </DataGrid>
    );
}
